<template>
  <!-- <body style="background-color: lightgrey;"> -->
  <div>
    <body style="background-color: lightgrey">
      <v-hover v-slot:default="{ hover }" open-delay="200">
        <v-card
          class="mx-auto "
          :elevation="hover ? 16 : 2"
          max-width="640px"
          max-height="940px"
          style="margin-top: 7%; border-radius: 20px;"
          outlined
          raised
        >

          <v-form ref="loginform" @submit.prevent="validate_login()">
            <v-row class="mt-12" align="center" justify="center">
              <v-col cols="12" align="center" justify="center">
                <!-- desktop -->
                <v-img
                  class="white--text align-end animated fadeInUp slow hidden-sm-and-down"
                  width="48%"
                  src="../assets/img/engineering.png"
                ></v-img>
                <!-- mobile-->
                <v-img
                  class="white--text align-end animated fadeInUp slow hidden-md-and-up mt-5"
                  width="55%"
                  src="../assets/img/engineering.png"
                ></v-img>
              </v-col>
              <v-col
                class="animated fadeInUp mt-8"
                cols="12"
                align="center"
                justify="center"
              >
                <v-col class="mt-5" cols="8" sm="9">
                  <v-text-field
                    color="red darken-1 red--text"
                    style="border-radius: 10px;"
                    align-center
                    v-model="username"
                    height="32"
                    prepend-inner-icon="mdi-account-circle"
                    label="username"
                    single-line
                    solo
                  ></v-text-field>
                  <v-text-field
                    color="red darken-1 red--text"
                    solo
                    style="border-radius: 10px;"
                    v-model="password"
                    prepend-inner-icon="mdi-lock"
                    :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                    :type="show1 ? 'text' : 'password'"
                    name="input-10-1"
                    label="password"
                    @click:append="show1 = !show1"
                  ></v-text-field>
                  <v-btn
                    v-on:click="validate_login()"
                    :loading="loading"
                    type="submit"
                    style="border-radius: 10px;"
                    block
                    color="white--text"
                    dark
                    large
                    >Login</v-btn
                  >
                </v-col>
              </v-col>
            </v-row>
          </v-form>
          <v-container>
            <v-row cols="9" style="margin-left: 8%">
              <v-col cols="5" sm="5"></v-col>

              <v-col cols="4" sm="4" style="margin-left: 25%"></v-col>
            </v-row>
          </v-container>
        </v-card>
      </v-hover>
      <v-dialog
        v-model="loadingDialog"
        fullscreen
        hide-overlay
        persistent
        transition="dialog-transition"
      >
        <v-card
          class="pa-0 ma-0 -webkit-full-screen"
          max-height="full screen"
          max-width="auto"
          width="100%"
          style="border-radius: 10px;"
          height="100%"
        >
          <v-row align="center" justify="center" width="100%">
            <v-col align="center" justify="center" style="margin-top: 15%">
              <!-- desktop -->
              <v-img
                class="white--text align-end hidden-sm-and-down"
                width="25%"
                src="../assets/img/engineering.png"
              ></v-img>
              <!-- mobile-->
              <v-img
                class="white--text align-end hidden-md-and-up mt-5"
                width="15%"
                src="../assets/img/engineering.png"
              ></v-img>
            </v-col>
          </v-row>
          <br />
          <br />
          <v-row align="center" justify="center" width="100%">
            <v-col cols="4">
              <br />

              <v-progress-linear
                color="black"
                indeterminate
                reverse
              ></v-progress-linear>

              <br />
            </v-col>
          </v-row>
        </v-card>
      </v-dialog>
    </body>
