</template>

<script>
// import ModelForgetPassword from '@/components/Login/ModelForgetPassword.vue'
// import ModelSignUP from '@/components/Login/ModelSignUP.vue'
import Swal from "sweetalert2";
export default {
  name: "Login",
  components: {
    // ModelForgetPassword,
    // ModelSignUP
  },
  data() {
    return {
      show1: false,
      loading: false,
      username: undefined,
      password: undefined,
      loadingDialog: false,
      counters: 0,
      user_login: "",
    };
  },
  watch: {},
  computed() {},
  created() {
    this.check_token_expired();
    this.$store.commit("data_interval_get_data", this.counters);
  },
  mounted() {},
  methods: {
    check_token_expired() {
      // console.log(localStorage.token);
      this.loading = false;
      this.axios({
        method: "GET",
        url: this.$store.state.url_axios + "api/v1/meters",
        headers: { Authorization: "Bearer " + localStorage.token },
      })
        .then((response) => {
          // console.log(response.data);
          var result = response.data;
          if (result.message == "success") {
            this.loading = false;
            this.$router.push("/DashboardDevice");
          }
        })
        .catch((error) => {
          //  console.log(error.response.statusText);
          if (error.response.data.error_message === "Measurement Not Found") {
            Swal.fire({
              icon: "error",
              title: "Oops...",
              text: "No data",
            });
          } else if (error.response.statusText === "UNAUTHORIZED") {
            this.$router.push("/login");
            this.$message.warning("Time out !!!");
          } else if (error.response.statusText === "UNPROCESSABLE ENTITY") {
            this.$router.push("/login");
          } else {
            this.$router.push("/login");
          }
        });
    },
    Open_ModelForgetPassword() {
      this.$refs.ModelForgetPassword.open();
    },
    Open_ModelSignUP() {
      this.$refs.ModelSignUP.open();
    },
    open_dialogloading() {
      this.$refs.loadingDialog.open();
    },
    validate_login() {
      if (this.username === undefined || this.password === undefined) {
        Swal.fire({
          icon: "error",
          title: "Oops...",
          text: "Something went wrong!",
          footer: "<a>Please check username and password</a>",
        });
      } else {
        this.button_login();
      }
    },
    button_login() {
      this.loadingDialog = true;

      this.axios
        .post(this.$store.state.url_axios + "api/v1/users/login", {
          username: this.username,
          password: this.password,
        })
        .then(
          (response) => {
            // console.log(response);
            var result = response.data;
            //  console.log(result);
            if (result["message"] === "success") {
              localStorage.token = result["result"].access_token;
              localStorage.refresh_token = result["result"].refresh_token;
              this.save_user_login();
              this.loadingDialog = false;
              this.$router.push("/DashboardDevice");
            } else {
              this.$swal("กรุณาตรวจสอบ Username และ Password", "", "error");
              this.loadingDialog = false;
            }
          },
          (response) => {
            if (response.message === "Request failed with status code 400") {
              this.loadingDialog = false;
              this.$swal("กรุณากรอกข้อมูลให้ครบถ้วน", "", "warning");
            } else if (
              response.message === "Request failed with status code 500"
            ) {
              this.loadingDialog = false;
              this.$swal(
                "เกิดข้อผิดพลาด กรุณาตรวจสอบการเชื่อมต่อ",
                "",
                "error"
              );
            } else if (
              response.message === "Request failed with status code 401"
            ) {
              this.loadingDialog = false;
              this.$swal("กรุณาตรวจสอบ Username และ Password", "", "error");
              this.$router.push("/login");
            } else if (
              response.message === "Request failed with status code 422"
            ) {
              this.loadingDialog = false;
              this.$swal("กรุณาตรวจสอบ Username และ Password", "", "error");
              this.$router.push("/login");
            } else if (
              response.message === "Request failed with status code 404"
            ) {
              this.loadingDialog = false;
              this.$swal("กรุณาตรวจสอบ Username และ Password", "", "error");
              this.$router.push("/login");
            } else if (response.message === "Network Error") {
              this.loadingDialog = false;
              this.$swal("กรุณาตรวจสอบการเชื่อมต่อ", "", "warning");
            }
            this.loadingDialog = false;
          }
        )
        .catch((error) => {
          if (error.response.data.error_message === "Measurement Not Found") {
            this.detdata_default_power();
            Swal.fire({
              icon: "error",
              title: "Oops...",
              text: "ไม่พบข้อมูล",
            });
          } else if (error.response.statusText === "UNPROCESSABLE ENTITY") {
            this.$message.warning("Please sign in again");
            this.$router.push("/login");
          } else {
            this.detdata_default_power();
            Swal.fire({
              icon: "error",
              title: "Oops...",
              text: "กรุณาตรวจสอบการเชื่อมต่อ",
            });
          }
        });
    },
    save_user_login() {
      var user_login = this.username;
      window.localStorage.setItem("user_login", JSON.stringify(user_login));
    },
  },
};
</script>

